@import "sass/index";

.title {
  font-family: "Gilroy";
  font-style: italic;
  font-weight: 900;
  font-size: 20px;
  line-height: 25px;
  color: use(--template-modal-title-color);
}

.title_and_buttons_container {
  display: flex;
  align-items: center;
  margin: 30px 0 15px 10px;
}

.buttons_container {
  margin-left: 10px;
}

.slideElement {
  width: 170px;
  height: 95px;
  overflow: hidden;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: use(--info-dark);
  padding: 5px;
}
.imgElement {
  width: 100%;
  max-height: 100%;
  max-width: 130px;
  object-fit: contain;
  mix-blend-mode: hard-light;
}
