.root {
  width: 100%;
  margin-top: 10px;
  padding: 10px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
}

.descriptionContainer {
  height: max-content;
  display: flex;
  gap: 10px;
}

.imageContainer {
  width: 100px;
  height: 100px;

  & > img {
    border-radius: 6px;
    width: 100%;
    height: 100%;
  }
}

.wrapper {
  width: 150px;
  display: flex;
  flex-direction: column;
}

.title {
  font-size: 20px;
  line-height: 130%;
  font-weight: bold;
  color: #ffffff;
}

.description {
  margin-top: 12px;
  font-size: 14px;
  line-height: 130%;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
}

.rewardContainer {
  height: 50px;
  margin-top: 20px;
  display: flex;
  gap: 10px;

  & > div {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.rewardTitles {
  width: 100px;
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: rgba(255, 255, 255, 0.7);
}

.rewardValuesWrapper {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
}

.coinImg {
  position: inherit;
  width: 16px;
  height: 16px;
}

.reward {
  display: flex;
  align-items: center;
  gap: 4px;
}
