@import "sass/index";

.container {
  display: flex;
  align-items: center;
  width: 100%;
  height: auto;
  margin-top: 30px;
  margin-bottom: 20px;
  @media (max-width: $fullHDScreen) {
    .inner {
      grid-template-columns: repeat(2, 256px);
      grid-template-rows: repeat(2, 58px);
    }
  }
  @media (max-width: $mobileScreen) {
    .inner {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 38px);
      width: 100%;
      margin: 0 5px;
    }
  }
}
.inner {
  display: grid;
  align-items: center;
  grid-template-columns: repeat(4, 256px);
  grid-template-rows: 58px;
  gap: 9px;
  margin: 0 auto;
  height: 100%;
}
