@import "sass/index";

.root {
  margin: 0 15px;

  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.button {
  background: use(--info-dark) !important;
  border-radius: 50%;
  box-shadow: none;
  padding: 0;
  width: 32px;
  height: 32px;

  color: use(--template-modal-title-color);

  &:last-child {
    margin-left: 5px;
  }
}
