@import "sass/index";

$desktopCard: 164px;
$mobileCard: 159px;

$mobileWidth: 400px;

$gap: 10px;

.listRoot {
  justify-content: space-evenly;
  padding: 10px 8px;
  gap: $gap;
  display: grid;
  width: 100%;
  grid-template-columns: repeat(var(--columnCount), $desktopCard);
  @media (max-width: $mobileWidth) {
    grid-template-columns: repeat(
      var(--columnCount),
      calc(100vw / 2 - #{$gap})
    );
  }
}
.flex {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.bigFlex {
  grid-column: 1 / span 2;
  grid-row: auto / span 1;
}
.grid {
  grid-area: defaul;
}
.labelWrapper {
  width: $desktopCard;
  overflow: hidden;
  text-overflow: ellipsis;
  @media (max-width: $mobileWidth) {
    width: calc((100vw - 30px) / 2);

    max-width: $mobileCard;
  }
}
.rootBanner {
  border-radius: 30px;
  overflow: hidden;
  background: transparent !important;
  transition: transform 0.5s;

  &:hover {
    transform: scale(1.05);
  }
}

.label {
  padding: 0;
  margin: 0;
  font-family: "Gilroy";
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  color: use(--template-modal-title-color);
  width: calc(#{$desktopCard} - 40px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  text-transform: capitalize;

  @media (max-width: $mobileWidth) {
    width: calc(#{$mobileCard} - 40px);
  }
}

.bannerRoot,
.bannerImg {
  width: $desktopCard;
  height: $desktopCard;
  object-fit: cover;

  @media (max-width: $mobileWidth) {
    width: calc((100vw - 30px) / 2);
    height: auto;

    max-width: $mobileCard;
    max-height: $mobileCard;
    padding: 0;
  }
}
.emptyBannerImg {
  width: $desktopCard;
  height: $desktopCard;
  @media (max-width: $mobileWidth) {
    width: calc((100vw - 30px) / 2);

    max-width: $mobileCard;
    height: $mobileCard;
  }
}

.bigBanner,
.bigImg {
  // width: calc(2 * #{$desktopCard} + #{$gap});
  width: 100%;
  height: $desktopCard;
  padding: 0;

  img {
    width: 100%;
  }

  @media (max-width: $mobileWidth) {
    width: calc(100% - 4px);
    height: auto;
    max-width: 328px;
    max-height: $mobileCard;
    padding: 0;
    margin: 0 auto;
  }
}
.bigBannerLabel {
  width: calc(100% - 8px);
  @media (max-width: $mobileWidth) {
    max-width: 328px;
  }
}

.continueContent {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 9px 0px 10px 10px;
  margin-bottom: 5px;
  width: calc(100% - 10px);
}

.link {
  text-decoration: none;
}

:export {
  #{"desktopCard"}: $desktopCard;
  #{"mobileCard"}: $mobileCard;
}
