.AffiliatePageFallback_block {
  display: grid;
  width: 100%;
  grid-gap: 20px;
  gap: 20px;
  min-height: 90vh;
  grid-template-columns: 1.3fr 0.7fr;
}

.AffiliatePageFallback_box {
  height: 100%;
  width: 100%;
  border-radius: 10px;
  overflow: hidden;
}

@media (max-width: 1300px) {
  .AffiliatePageFallback_block {
    grid-template-columns: 1fr;
  }
}
