@import "sass/index";

$shadow: inset 0 -4px 0 0 rgba(0, 0, 0, 0.18);

.button.button {
  width: 100%;
  font-size: spacing(2.25);
  height: spacing(7.5);
  border-radius: spacing(1);
  margin-top: spacing(3.5);

  box-shadow: $shadow;

  color: #ffffff;
  background-color: $m_button-color;
  &:hover {
    background-color: $m_button-color-highlight;
    box-shadow: $shadow;
  }
  &:focus {
    background-color: $m_button-color-highlight;
    box-shadow: $shadow;
  }
  &:active {
    background-color: $m_button-color-highlight;
    box-shadow: $shadow;
  }
  &:disabled {
    background-color: rgba($m_button-color, 0.5);
    box-shadow: $shadow;
  }
}
.loadingWrapper.loadingWrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
