@import "sass/index";

.list {
  display: flex;
  overflow-x: scroll;
  margin: -5px;
  &::-webkit-scrollbar {
    display: none;
  }
}

.item {
  position: relative;
  width: 350px;
  padding: 5px;
  transition: all 0.4s;
  transform: scale(0.95);

  &:hover,
  &:focus,
  &:active {
    z-index: 1;
    transform: scale(1);
  }
}

.card_container {
  background: use(--info-dark);
  border-radius: 10px;
  height: 230px;
  padding: 13px;
}

.card_title {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 700;
  font-size: 20.0949px;
  line-height: 130%;
  letter-spacing: 0.01em;
  color: white;

  margin: 5px 0 18px 10px;
}

.card_content {
  display: flex;
}

.card_image {
  width: 158px;
  height: 113px;
  margin-right: 15px;
}

.card_description {
  font-family: "Gilroy";
  font-style: normal;
  font-weight: 500;
  font-size: 14.0664px;
  line-height: 130%;
  color: rgba(255, 255, 255, 0.7);
}

.gift {
  display: flex;
  margin-top: 12px;
}

.icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.text_decoration {
  color: $home-page-text-color;
  font-weight: 700;
}

.end_time {
  color: white;
}

.title {
  font-family: "Gilroy";
  font-style: italic;
  font-weight: 900;
  font-size: 20px;
  line-height: 25px;
  color: white;
}

.title_and_buttons_container {
  display: flex;
  align-items: center;
  margin: 30px 0 15px 10px;
}

.buttons_container {
  margin-left: 10px;
}

.coinImg {
  position: inherit;
  width: 16px;
  height: 16px;

  margin-bottom: -3px;
  margin-left: 3px;
}
