@import "sass/index";

.container {
  position: relative;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;
  background: use(--info-dark);
  width: fit-content;
  padding-left: 10px;
  padding-right: 10px;
  height: 40px;
  left: 0px;
  top: 0px;
  border-radius: 10px 10px 0 0;
  cursor: pointer;
  min-width: 40px;

  &.left {
    padding-left: 5px;
  }

  &.left::before {
    position: absolute;
    left: -9%;
    top: 20%;
    content: "";
    display: block;
    width: 10%;
    height: 80%;
    background: use(--info-dark);
    clip-path: polygon(100% 0, 100% 0%, 100% 100%, 0% 100%);
  }
  &.right::after {
    position: absolute;
    left: 100%;
    top: 20%;
    content: "";
    display: block;
    width: 10%;
    height: 80%;
    background: use(--info-dark);
    clip-path: polygon(0 0, 0 0%, 100% 100%, 0% 100%);
  }
}
.title {
  font-family: "Gilroy";
  font-style: italic;
  font-weight: 900;
  font-size: 20px;
  line-height: 1;
  color: use(--template-modal-title-color);

  text-transform: capitalize;
  letter-spacing: 0.3px;

  @media (max-width: $littleScreen) {
    font-size: 16px;
  }
}
