@import "sass/index";

.joinNowBlock {
  background: use(--template-page-joinBox-background);
  color: use(--template-page-joinBox-text);
  border-radius: 30px;
  max-width: 290px;
  height: 210px;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;

  &:not(:first-child) {
    // marginLeft: theme.spacing(2.5);
  }
}
.joinNowText {
  font-family: "Gilroy";
  font-weight: 800;
  width: 200px;
  font-size: 24px;
  color: use(--template-page-joinBox-text);
  letter-spacing: 0;
  text-align: center;
  line-height: 26px;
}
.joinNowButton {
  margin-top: spacing(3.75);

  font-family: "Gilroy";
  font-weight: bold;
  font-size: 15px;
  color: #2f2b2e;
  letter-spacing: 0.8px;
  text-align: center;

  background: #ffffff !important;
  border-radius: 25px;
  height: 50px;

  & > * {
    transition: transform 0.4s;

    &:hover {
      transform: scale(1.05);
    }
  }
}
