@import "sass/index";

.orSeparator {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
}

.orSeparatorText {
  color: use(--secondary-main, 0.75);
  margin: spacing(0) spacing(1);
}

.divider {
  background-position-x: use(--secondary-main, 0.33);
  flex: 1;
}

.socialMedia {
  margin: spacing(3) spacing(0);
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.socialMediaIcon {
  &:not(:first-child) {
    margin-left: spacing(2.75);
  }
}
