@import "sass/index";

.innerWrapper {
  max-width: 100%;
  padding: 0 0 spacing(4);
}
.loadingWrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.errorWrapper {
  margin-top: spacing(1.5);
}
.submitButton {
  margin-top: spacing(4);
}

.loadingBox {
  margin-top: spacing(3);
}
