@import "sass/index";

.root {
  display: flex;
  flex-direction: column;
  width: 100%;
  position: relative;
}
.item {
  display: block;
  margin: 0;
  box-sizing: border-box;
  text-transform: capitalize;
}
.contrast {
  position: absolute;
  height: 100%;
  width: 100%;
  background: black;
  opacity: 0.25;
}
.gameWrapper {
  position: absolute;
  top: 0;
  right: 0;
  width: 250px;
  height: 100%;
  padding-right: 20px;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  @media (max-width: $mobileScreen) {
    right: 50%;
    transform: translateX(50%);
    padding: 0;
  }
}
.betList {
  @media (max-width: $mobileScreen) {
    padding: 0 10px;
  }
}

.title {
  font-family: "Gilroy";
  font-style: italic;
  font-weight: 900;
  font-size: 20px;
  line-height: 25px;
  color: white;
  margin: 26px 0 15px 22px;
}

.christmas {
  margin-top: 10px;
}

.bannersFallBack {
  width: 100%;
  height: 200px;
}
