@import "sass/index";
@import "./variables.module.scss";

.container {
  margin-top: 20px;
}

.sliderWrapper {
  background: use(--info-dark);
  min-height: $cardWidth;

  border-radius: 0 0 10px 10px;
  padding: 5px;
}

.gameListImg {
  width: $cardWidth;
  height: $cardWidth;

  transform: scale(#{$scaleValue});

  &:hover {
    transform: scale(1);
  }
}

.withoutAllLink {
  border-top-right-radius: 10px;
}

.link {
  text-decoration: none;
}

.img {
  width: 100%;
  height: auto;
  transform: scale(#{$scaleValue});
  object-fit: cover;
  border-radius: 10px;
  transition: all 0.4s !important;
  &:hover {
    transform: scale(1);
  }
}
