.root {
  margin-top: 70px;
}

.logo {
  margin: 0 auto;
  max-width: 130px;
  width: 100%;
  height: 50px;
}

.title {
  margin: 45px auto 0;
  max-width: 700px;
  width: 75%;
  height: 70px;
}

.gameBlock {
  margin: 0 auto;
  max-width: 840px;
  width: 100%;
  height: 400px;
  border-radius: 20px;
  overflow: hidden;
}

.buttonBlock {
  max-width: 540px;
  width: 90%;
  height: 230px;
  border-radius: 0 0 38px 38px;
  overflow: hidden;
  margin: 0 auto;
}

.footer {
  margin: 180px auto 50px;
  height: 280px;
  width: 600px;
}
