@import "sass/index";

.wrapper.wrapper {
  position: relative;
  width: fit-content;
  max-height: 100%;
  max-width: 100vw;
  margin: 0 auto;
}
.root.root {
  width: 381px;
  max-width: 100vw;
  display: flex;
  justify-content: center;
  padding: 0 spacing(5.25);
  overflow: hidden;

  @media (max-width: 350px) {
    padding: 0 spacing(2);
  }
}
.header.header {
  padding: spacing(4) spacing(5.25) spacing(1.25);

  @media (max-width: 350px) {
    padding-left: spacing(2);
    padding-right: spacing(2);
  }
}
.innerWrapper.innerWrapper {
  width: min-content;
  padding: 0;
  //max-height: 100%;
  overflow: hidden;
}
.title.title {
  text-align: center;
  padding: 0 0 spacing(1);
  margin: 0 spacing(3);

  & > * {
    font-family: "Gilroy";
    font-weight: bold;
    font-size: spacing(3.5);
    line-height: 28px;
    color: white;
  }
}
.content.content {
  padding: spacing(0);
  margin-top: spacing(-1.25);
  overflow: visible;
  max-width: 100%;

  &:first-child {
    padding: spacing(0);
  }
}
.description.description {
  margin: 0;
  font-family: Helvetica;
  font-size: 13px;
  color: rgba(white, 0.5);
  text-align: center;
}
.form.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 spacing(1);
}
