@import "sass/index";

.item {
  display: flex;
  align-items: center;
  cursor: pointer;

  background: use(--info-dark);
  border-radius: 10px;
  padding: 13px 25px 15px;

  width: 100%;
  min-width: min-content;
  max-width: 256px;
  height: 100%;

  transition: background 0.3s;

  & .text {
    transition: color 0.3s;
  }

  @media (max-width: $mobileScreen) {
    padding: 9px 9.88px 9.25px;
    img {
      width: 19.75px;
    }
  }

  &:hover {
    background: rgba(0, 0, 0, 0.5);
  }
  &:hover .text {
    color: rgba(52, 231, 255, 1);
  }
}

.text {
  font-weight: 500;
  font-size: 18px;
  line-height: 21px;
  color: use(--template-modal-title-color);
  white-space: nowrap;
  @media (max-width: 600px) {
    font-size: 11.8516px;
    line-height: 14px;
  }
}

.icon {
  width: 31px;
  height: auto;
  margin-right: 9.72px;

  @media (max-width: $mobileScreen) {
    margin-right: 6.4px;
  }
}
